'use server';

import { GoogleGenerativeAI } from '@google/generative-ai';

export async function generateManagementPlan(formData: FormData) {
  const apiKey = process.env.GEMINI_API_KEY;
  if (!apiKey) throw new Error("APIキーが見つかりません");

  const genAI = new GoogleGenerativeAI(apiKey);

  // ▼▼▼ 自動切り替えリスト（メンテナンス不要） ▼▼▼
  // 優先順位：安くて速い順（1.5 Flash）→ 予備 → 将来の2.0 → 汎用 → 最後の砦（Pro）
  const modelCandidates = [
    'gemini-1.5-flash',          // ① 本命（約0.5円/回）
    'gemini-1.5-flash-latest',   // ② 予備
    'gemini-2.0-flash',          // ③ 未来
    'gemini-flash-latest',       // ④ 汎用
    'gemini-pro'                 // ⑤ 最後の砦（これが動けば絶対大丈夫）
  ];

  // --- 入力項目の取得 ---
  const industry = formData.get('industry') as string || '（未入力：業界知識をもとに補完してください）';
  const businessDetail = formData.get('businessDetail') as string || '（未入力：一般的な業務内容として補完してください）';
  const hqLocation = formData.get('hqLocation') as string || '（未入力：一般的な地方都市として想定してください）';
  const otherLocations = formData.get('otherLocations') as string || 'なし';
  const businessArea = formData.get('businessArea') as string || '（未入力：一般的な商圏として補完してください）';
  const employeeCount = formData.get('employeeCount') as string || '（未入力：小規模事業者として想定してください）';
  const futureEmployeeCount = formData.get('futureEmployeeCount') as string || 'なし（現状維持）';
  const strengths = formData.get('strengths') as string || '（未入力：この業種の一般的な強みを補完してください）';
  const issues = formData.get('issues') as string || '（未入力：この業種の一般的な課題を補完してください）';
  const newInitiatives = formData.get('newInitiatives') as string || '（未入力：特になし）';
  const targetCustomers = formData.get('targetCustomers') as string || '（未入力：一般的なターゲット層を想定してください）';
  const acquisitionChannel = formData.get('acquisitionChannel') as string || '（未入力：一般的な集客経路を想定してください）';
  const vision = formData.get('vision') as string || '（未入力：持続的な成長と地域貢献）';

  // ▼▼▼ プロンプト（入力分析・定義厳守・省略禁止版） ▼▼▼
  const prompt = `
あなたは**入力情報を忠実に分析し、勝てる戦略を立案する**敏腕経営コンサルタントです。 私は社員20名以下の小規模企業の経営者です。
私の会社の強みや特徴を最大限に活かした『経営計画書（全8章）』を作成してください。

【極めて重要な依頼】
今回は**「第1章から第8章まで」**を作成してください。全章において省略をせず、第8章の施策50項目についても具体的な文章で書き出してください。

【入力情報（ヒアリングシート）】
1. 業種： ${industry}
2. 事業内容の詳細： ${businessDetail}
3. 本社所在地： ${hqLocation}
4. その他拠点： ${otherLocations}
5. 営業エリア（商圏）： ${businessArea}
6. 現在の社員数： ${employeeCount}
7. 5年後の希望社員数： ${futureEmployeeCount}
8. 顧客から褒められる点（強み）： ${strengths}
9. 困っている点（課題）： ${issues}
10. これから新しくやりたいこと： ${newInitiatives}
11. どんなお客様に来てもらっているか： ${targetCustomers}
12. お客様の流入経路： ${acquisitionChannel}
13. どんな会社にしていきたいか（想い）： ${vision}
---------------------------

🏗️ 経営計画書の構成（全8章）
1. 会社概要
2. これから目指す姿（将来構想）
3. 今の状況を知る（SWOT分析）： 強み・弱み・機会・脅威を各3項目以上、Markdown表で提示。
4. 分析を活かす作戦（クロスSWOT）： SO・WO・ST・WTを各3項目以上、Markdown表で提示。
5. 私たちの立ち位置（戦略の優先順位）： ★後述の「選択ルール」に従い、入力情報を分析してAIが決定。
6. 中期目標： 5年後までの年度別目標を具体的に提示。
7. 商圏・市場戦略： 地域密着か広域か、具体的なターゲットを含めて提示。
8. 戦略と施策： 5区分すべてを10項目ずつ（計50項目）、具体的な行動リストとして出力。

📋 第5章「私たちの立ち位置」の選択ルール（★入力分析ロジック★）
以下の「5つの要素」の定義に基づき、**入力された「事業内容の詳細（${businessDetail}）」、「強み（${strengths}）」、「業種（${industry}）」の3点を深く分析し**、この会社が最も勝てる組み合わせをAIが判断して選択してください。

**「答えありき」の選択は禁止です。入力内容によって毎回最適解を変えてください。**

**【5つの要素の定義（ここを厳密に解釈すること）】**
* **商品**：提供する物品、または**提供する役務（サービスそのもの）**の品質、機能、正確さ、ブランドとしての信頼性。
* **サービス**：**契約や購入の前後に付随する対応の質**。マナー、アフターフォロー（フォロー体制含む）、気配りなど。
* **価格**：市場相場との整合性や、“納得感”のある値付け。
* **アクセス**：物理的な利便性（立地・反応の速さ）だけでなく、**専門的な情報やスキル・解決策への橋渡し（知的利便性）**を含む。
* **経験価値**：顧客が抱く精神的な満足感や安心感。（※構築難易度が高いため、基盤要素とする）

**【選択基準：入力情報の重み付け】**

1. **【最優先（市場支配レベル - 1つ）】**
   * 事業内容と強みを分析し、**他社を圧倒できる最も際立った要素**を選んでください。
   * 「独自の技術、こだわりの品質、作業（役務）の正確さ」なら → **商品**
   * 「納得感のある価格設定、圧倒的なコスパ」なら → **価格**
   * 「スピード対応、立地」または**「専門的な解決策・情報の提供（知的アクセス）」**なら → **アクセス**
   * 「契約前後の神対応、徹底したマナー・盤石なフォロー体制」なら → **サービス**
   * **禁止事項：「経験価値」は最優先には選ばないでください（顧客主観であり構築が難しいため）。**

2. **【優先（差別化レベル - 1つ）】**
   * 2番目に強い要素、または最優先要素を支える要素を選んでください。
   * **禁止事項：「経験価値」はここにも選ばないでください。**

3. **【基盤（一般レベル - 3つ）】**
   * 選ばれなかった残りの3つをここに配置してください。
   * **「経験価値」は必ずここに含めてください**（意味合い：不快感を与えない最低限の安心感）。

📋 第8章「戦略と施策」の5区分（各10項目・計50項目すべて出力）
1. 商品・サービスをどうするか
2. お客様にどう伝えるか（営業・販促）
3. 仕事の進め方と品質
4. 働く仲間とチームづくり
5. ムダをなくすデジタル活用

🗣️ 出力トーンと形式
トーン： 正式文書トーン。難しい言葉を避け、自然体で読みやすく。
形式： Googleドキュメントに貼り付け可能なMarkdown形式。
**【重要】読みやすさを確保するため、各段落や項目の間には十分な改行を入れて出力してください。**

【出力後の案内】 
計画書の本文が終わった後に、区切り線（---）を引き、必ず以下の形式で利用者への案内を出力してください（改行も再現すること）。

---
**【この経営計画書の使い方】**

この計画書は、Googleドキュメント等に貼り付けてそのままお使いいただける形式で作成しました。

1. **保存方法（重要）**
   画面上に表示されている**「全文をコピーする」ボタン**を押してください。
   計画書の内容がすべてコピーされますので、その後GoogleドキュメントやWordに貼り付けて保存してください。

2. **活用のアドバイス**
   スライド資料にする場合は、この計画書を原稿として利用するか、専門業者への依頼資料としてご活用ください。

3. **免責事項**
   本内容はAIが業界知識やヒアリング情報を基に作成した「参考情報」です。
   売上高や利益計画などの詳細な「数値計画」は出力されません。
   経営実態と異なる箇所がないかご自身で確認・修正し、最終的な判断は利用者様の責任において行ってください。
`;

  let lastError = null;

  // リストのモデルを順番に試すループ処理
  for (const modelName of modelCandidates) {
    try {
      // 接続テスト
      const model = genAI.getGenerativeModel({ model: modelName });
      const result = await model.generateContent(prompt);
      const response = await result.response;
      
      // 成功したらループを抜けて結果を返す（ここで終了）
      return response.text();

    } catch (error: any) {
      // 失敗したら、コンソールに警告だけ出して次のモデルへ
      console.warn(`⚠️ モデル ${modelName} は使用できませんでした。次を試します...`);
      lastError = error;
      // loop continues...
    }
  }

  // 全部のモデルが全滅した場合のみエラーを出す
  console.error("全モデル全滅:", lastError);
  
  // ▼▼▼ ユーザーに不安を与えず、適切な行動（待機）を促すメッセージ ▼▼▼
  throw new Error("現在、アクセス集中により応答しづらくなっています。恐れ入りますが、しばらく時間（1〜2分程度）を空けてから、再度お試しください。");
}